import Vue from 'vue';
import Router from 'vue-router';
import plataforma_rotas from './plataforma';

Vue.use(Router);

const routes = [
    ...plataforma_rotas
]


const router =  new Router({
  mode: 'history',
  base: process.env.NODE_ENV === 'production' ? '/OrtoLink/' : '/',
  routes,
});

export default router
